.like-container {
  color: white;
  background-color: #31363F;
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
    padding: 7px 10px;
    margin-bottom: -20px;
    .like-comment {
      color: white;
      margin-top: -8px;
      display: grid;
      grid-template-columns: 33.33% 33.33% 33.33%;
    }
    .comments{
      color: white;
      margin-left: auto;
    }

    .comment-input {
      height: 40px;
      background-color: #31363F;
      padding-left: 10px;
      border-radius: 30px;
      border: 1px solid #737373;
      font-family: system-ui;
      color: white;
      font-size: 16px;
    }
  
    .comment-input:focus {
      outline: 1px solid #4b4b4b;
      color: white;
    }
    .add-comment-btn {
      width: 150px;
      color: white;
      height: 35px;
      background-color: #0a66c2;
      outline: none;
      border: none;
      border-radius: 30px;
      cursor: pointer;
      margin-top: 15px;
      margin-bottom: 15px;
    }
    .commenter-image {
      color: white;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      cursor: pointer;
      object-fit: cover;
    }
    .all-comments {
      color: white;
      display: flex;
      justify-content: center;
      flex-direction: column;
      background-color: #31363F;
      border-radius: 10px;
      position: relative;
      margin-left: 5px;
      width: 100%;
      .name {

        color: white;
        margin-left: 10px;
        text-decoration: none;
      }
      .headline{
        color: white;
        margin-left: 10px;
      }
  
      .comment {
        margin-left: 15px;
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 17px;
        color: white;
      }
  
      .timestamp {
        color: white;
        position: absolute;
        right: 10px;
        top: 3px;
      }
    }
    .hr-line {
      color: white;
      margin-top: 0px;
      hr {
        border-top: 1px solid rgb(181, 181, 181);
      }
    }
    p {
      font-family: system-ui;
      font-weight: 400;
      color: white;
      font-size: 14px;
      margin-left: 10px;
    }
  
    .likes-comment-inner {
      display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 35px;
        background-color: #31363F;
        color: white;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        outline: none;
        font-size: 14px;
        font-weight: 600;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: -3px;

      
      .blue {
        color: #0a66c2;
        padding-top: 5px ;
      }
  
      .black {
        color: rgba(58, 58, 58, 0.835);
        padding-top: 5px ;
      }

      .Like{
        color: #0a66c2;
        padding-top: 5px ;
      }

      .Heart{
        color: red;
        padding-top: 5px ;
      }

      .Celebration{
        color: #c9660f;
        padding-top: 5px ;
      }

      .Mind_Blowing{
        color: #f8bf06;
        padding-top: 5px ;
      }

      p {
        font-family: system-ui;
        font-weight: 600;
        color: white;
        font-size: 16px;
      }
    }
  }

  .like-container {
    position: relative;
    margin-top: 0px;
    .reactions-comments-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: -8px;
  }

  }

  .hover-icons {
    display: none;
    position: absolute;
    top: -2px;

    left: 55px;
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    & > * {
        margin-right: 5px;
      }
  }
  
  .likes-comment-inner:hover .hover-icons {
    display: flex;
  }
  .likes-comment-inner:hover {
    background-color: #1c1919;
  }
  .share{
    color: white;
  }
  .reactions {
    color: white;
    font-size: 10px;
    text-decoration: none;
    transition: color 0s;
  }
  
  .reactions:hover {
    color: #2980b9; 
    text-decoration: underline;
    cursor: pointer;
  }
  
  .inactive:hover {
    color: white;
    text-decoration: none;
  }

  .comments {
    color: white;
    font-size: 14px;
    text-decoration: none;
    transition: color 0s;
    cursor: pointer;
  }
  
  .comments:hover {
    color: #2980b9;
    text-decoration: underline;
  }
  
.icon{
  color: white;
  background-size: cover;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

