.trash-button{
  position: relative;
  top:4px;
}
.load-more-btn {
  display: flex;
  align-items: center;
  width: 170px;
  height: 35px;
  background-color: rgb(41, 48, 137);
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  font-size: 14px;
  font-weight: 600;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: -3px;
  padding-left: 10px;
  font-size: 15px;
}

.load-more-btn:hover {
background-color: #dcdbdb;
color:black;
}

.commenter-container {
  color: white;
  display: flex;
  border: 1px solid #18191b;
  align-items: flex-start;
  padding:10px;
  margin-right: 10px;
}
